import React from 'react'
import Toggle from 'react-toggled'
import parseURL from 'url-parse'
import ReactMarkdown from 'react-markdown'
import removeMd from 'remove-markdown'
import TextTruncate from 'react-text-truncate'
import Media from 'react-media'
import { Link } from 'gatsby'

import ZoomImg from '../components/ZoomImg'
import ExternalLink from '../components/ExternalLink'
import { WideText } from '../styles/typography'

import { media, space, colors } from '../styles/theme'
import { rem } from '../styles/tools'
import categories from '../categories'

const CategoryLabel = ({ isOpen, category, bottom }) => (
  <div
    css={{
      position: 'absolute',
      zIndex: 1,
      left: isOpen ? rem(24) : '58%',
      top: bottom ? null : 0,
      bottom: bottom ? 0 : null,
      [media._sm]: {
        left: '100%',
        paddingTop: '24%',
        transform: 'translateY(-50%)',
      },
    }}
  >
    <div
      css={{
        padding: `${rem(4)} ${rem(16)}`,
        fontWeight: 800,
        fontSize: rem(12),
        color: 'white',
        whiteSpace: 'nowrap',
        background:
          category === 'sponsored'
            ? colors.bg.blue.dark
            : categories[category].color,
        borderRadius: bottom ? '7px 7px 0 0' : '0 0 7px 0',
        border: bottom ? 'solid 1px' : null,
        borderRight: bottom ? null : 'solid 1px',
        borderBottom: bottom ? 'none' : 'solid 1px',
        [media._sm]: {
          padding: `${rem(16)} ${rem(4)}`,
          writingMode: 'vertical-lr',
          borderRadius: '0 5px 5px 0',
        },
      }}
    >
      {category === 'sponsored' ? 'Sponsored' : categories[category].label}
    </div>
  </div>
)

const Sender = ({ senderName, senderURL, media, ...props }) =>
  (senderName || senderURL) && (
    <div
      css={{
        display: 'none',
        [media]: { display: 'block' },
        marginTop: rem(13),
        fontSize: rem(13),
        color: colors.text.blue.light,
      }}
      {...props}
    >
      Submitted by:{' '}
      {senderURL ? (
        <ExternalLink noindex href={senderURL} css={{ fontWeight: 800 }}>
          {senderName || senderURL}
        </ExternalLink>
      ) : (
        <strong css={{ fontWeight: 800 }}>{senderName}</strong>
      )}
    </div>
  )

const Post = ({
  data: { title, url, img, senderName, senderURL, description, category },
  ...rest
}) => {
  const { hostname, pathname, query, hash } = parseURL(url)
  const externalURL = `${url}?utm_source=tldrmarketing.com&utm_medium=referral`
  const fullURL = hostname + pathname + query + hash

  return (
    <Toggle>
      {({ on, setOn, setOff }) => (
        <li
          css={{
            position: 'relative',
            [media._sm]: { padding: rem(20), paddingBottom: 0 },
            background: 'white',
            [media.max._sm]: {
              ...(on && {
                borderBottom: 'none !important',
                marginTop: rem(30),
                marginBottom: `${rem(30)} !important`,
              }),
            },
          }}
          {...rest}
        >
          <Media query="(min-width: 940px)">
            {matches =>
              matches ? (
                <CategoryLabel isOpen={on} category={category} />
              ) : (
                !on && <CategoryLabel isOpen={on} category={category} />
              )
            }
          </Media>
          <article
            css={{
              display: 'flex',
              alignItems: 'center',
              flexWrap: on ? 'wrap' : 'nowrap',
              [media._sm]: { flexWrap: 'nowrap' },
            }}
          >
            <div
              css={{
                flexBasis: on ? '100%' : '58%',
                order: on ? 2 : 1,
                padding: space.md,
                paddingBottom: space.sm,
                [media.mobileLg]: {
                  padding: rem(24),
                  paddingBottom: rem(12),
                },
                [media._sm]: {
                  flexBasis: '65%',
                  padding: rem(12),
                  paddingRight: rem(24),
                  order: 1,
                },
                overflow: 'hidden',
              }}
            >
              <Media query="(min-width: 940px)">
                {matches => (
                  <Toggle>
                    {({
                      on: showFullURL,
                      setOn: onMouseOver,
                      setOff: onMouseLeave,
                    }) => (
                      <ExternalLink
                        {...matches && { onMouseOver, onMouseLeave }}
                        href={externalURL}
                        css={{
                          display: 'block',
                          borderRadius: '2px',
                          ':hover': {
                            boxShadow: `0 0 0 5px white, 0 0 0 8px ${
                              colors.border.blue.normal
                            }`,
                          },
                        }}
                      >
                        {on ? (
                          <h3
                            css={{
                              fontWeight: 800,
                              fontSize: rem(18),
                              [media._sm]: { fontSize: rem(19) },
                              color: colors.text.grey.heading,
                            }}
                          >
                            {title}
                          </h3>
                        ) : (
                          <TextTruncate
                            element="h3"
                            line={2}
                            truncateText="â€¦"
                            text={title}
                            css={{
                              fontWeight: 800,
                              fontSize: rem(16),
                              [media._sm]: { fontSize: rem(19) },
                              color: colors.text.grey.heading,
                            }}
                          />
                        )}
                        <span
                          css={{
                            display: 'block',
                            marginTop: rem(3),
                            fontSize: rem(14),
                            color: colors.text.grey.body,
                            whiteSpace: 'nowrap',
                            textOverflow: 'ellipsis',
                            overflow: 'hidden',
                          }}
                        >
                          {showFullURL ? fullURL : hostname}
                        </span>
                      </ExternalLink>
                    )}
                  </Toggle>
                )}
              </Media>
              {on ? (
                <ReactMarkdown
                  renderers={{
                    link: ExternalLink,
                  }}
                  source={description}
                  css={{
                    marginTop: rem(13),
                    fontSize: rem(16),
                    lineHeight: 1.6,
                    color: colors.text.grey.body,
                    '> * + *': {
                      marginTop: rem(16),
                    },
                    li: {
                      listStyle: 'disc',
                      marginLeft: rem(36),
                    },
                    a: {
                      color: colors.text.blue.medium,
                      textDecoration: 'underline',
                    },
                  }}
                />
              ) : (
                <TextTruncate
                  element="p"
                  line={2}
                  truncateText="â€¦"
                  text={removeMd(description)}
                  css={{
                    [media.max._sm]: {
                      display: 'none',
                    },
                    marginTop: rem(13),
                    fontSize: rem(16),
                    lineHeight: 1.6,
                    color: colors.text.grey.body,
                  }}
                />
              )}
              <div css={{ display: 'flex' }}>
                {on ? (
                  <WideText
                    as="button"
                    onClick={setOff}
                    css={{
                      flex: 1,
                      paddingTop: rem(16),
                      paddingLeft: 0,
                      textAlign: 'left',
                      cursor: 'pointer',
                      outline: 'none',
                      fontSize: rem(10),
                    }}
                  >
                    Show less{' '}
                    <svg
                      css={{
                        width: rem(9),
                        height: rem(6),
                        [media._sm]: {
                          width: rem(10),
                          height: rem(7),
                        },
                      }}
                      viewBox="0 0 10 7"
                      fill="none"
                      stroke="currentColor"
                    >
                      <path
                        d="M0 0L4 4L8 0"
                        transform="translate(9 6) rotate(-180)"
                        strokeWidth="1.6"
                      />
                    </svg>
                  </WideText>
                ) : (
                  <WideText
                    as={Link}
                    to={`/links/abc123`}
                    rel="nofollow"
                    onClick={e => {
                      e.preventDefault()
                      setOn()
                    }}
                    css={{
                      flex: 1,
                      paddingTop: rem(16),
                      paddingLeft: 0,
                      fontSize: rem(10),
                    }}
                  >
                    Show more{' '}
                    <svg
                      css={{
                        width: rem(9),
                        height: rem(6),
                        [media._sm]: {
                          width: rem(10),
                          height: rem(7),
                        },
                      }}
                      viewBox="0 0 10 7"
                      fill="none"
                      stroke="currentColor"
                    >
                      <path
                        d="M0 0L4 4L8 0"
                        transform="translate(1 1)"
                        strokeWidth="1.6"
                      />
                    </svg>
                  </WideText>
                )}
                {on && (
                  <Sender
                    media={media.max._sm}
                    senderName={senderName}
                    senderURL={senderURL}
                  />
                )}
              </div>
            </div>
            <div
              css={{
                alignSelf: 'stretch',
                flexBasis: on ? '100%' : '42%',
                order: on ? 1 : 2,
                position: 'relative',
                [media.max._sm]: {
                  '::after': {
                    content: '""',
                    width: '100%',
                    height: '1px',
                    position: 'absolute',
                    bottom: 0,
                    backgroundColor: 'rgba(0, 0, 0, .1)',
                  },
                },
                [media._sm]: {
                  alignSelf: 'flex-start',
                  flexBasis: '35%',
                  marginBottom: rem(20),
                  order: 2,
                },
              }}
            >
              {img ? (
                <Media query="(min-width: 940px)">
                  {matches =>
                    matches ? (
                      <ZoomImg img={{ ...img, msrc: img.src }} />
                    ) : on ? (
                      <img
                        src={img.src}
                        alt={img.alt}
                        css={{
                          display: 'block',
                          width: '100%',
                        }}
                      />
                    ) : (
                      <div
                        onClick={setOn}
                        role="img"
                        aria-label={img.alt}
                        css={{
                          height: '100%',
                          position: 'relative',
                          zIndex: 0,
                          backgroundImage: `url(${img.src})`,
                          backgroundPosition: 'center',
                          backgroundSize: 'cover',
                          '::after': {
                            content: '""',
                            display: 'block',
                            width: '100%',
                            height: 0,
                            position: 'relative',
                            zIndex: 1,
                            paddingBottom: '58%',
                          },
                        }}
                      />
                    )
                  }
                </Media>
              ) : (
                <Media query="(min-width: 940px)">
                  {matches =>
                    on && !matches ? (
                      <div css={{ height: rem(30) }} />
                    ) : (
                      <div
                        onClick={setOn}
                        css={{
                          height: '100%',
                          position: 'relative',
                          zIndex: 0,
                          backgroundColor: colors.bg.grey.lightest,
                          [media._sm]: {
                            backgroundColor: 'transparent',
                            border: `solid 1px ${colors.border.blue.light}`,
                            borderRadius: '5px',
                          },
                          '::before': {
                            content: '"- No media -"',
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',
                            width: '100%',
                            height: '100%',
                            position: 'absolute',
                            color: colors.text.blue.light,
                            fontWeight: 900,
                            fontSize: rem(10),
                            textTransform: 'uppercase',
                            letterSpacing: rem(4),
                          },
                          '::after': {
                            content: '""',
                            display: 'block',
                            width: '100%',
                            height: 0,
                            position: 'relative',
                            zIndex: 1,
                            paddingBottom: '58%',
                          },
                        }}
                      />
                    )
                  }
                </Media>
              )}
              {on && (
                <Sender
                  media={media._sm}
                  senderName={senderName}
                  senderURL={senderURL}
                />
              )}
            </div>
          </article>
        </li>
      )}
    </Toggle>
  )
}

export default Post
