import styled from 'styled-components'
import { Link } from 'gatsby'

import { media, space, colors } from './theme'
import { rem } from './tools'

export const Logo = styled(Link)({
  display: 'flex',
  alignItems: 'center',
  fontWeight: 800,
  fontSize: rem(20),
  lineHeight: 1,
  color: colors.text.blue.dark,
  background: 'rgba(239, 245, 248, 0.9)',
  boxShadow: '0 0 30px 30px rgba(239, 245, 248, 0.9)',
})

export const MediumHeading = styled.span({
  fontWeight: 800,
  fontSize: rem(18),
  lineHeight: 1.2,
  color: colors.text.blue.dark,
})

export const WideText = styled.span(
  {
    display: 'inline-block',
    padding: space.md,
    color: colors.text.blue.light,
    fontWeight: 900,
    fontSize: rem(10),
    textTransform: 'uppercase',
    letterSpacing: rem(4),
    'a&, button&': {
      transition: '.2s',
      ':hover': {
        color: colors.text.blue.dark,
      },
    },
  },
  ({ verticalUp, verticalDown }) => ({
    writingMode: (verticalUp || verticalDown) && 'vertical-lr',
    transform: verticalUp && 'rotate(180deg)',
  })
)

export const CategoryLink = styled(Link)(
  {
    display: 'flex',
    alignItems: 'center',
    padding: `${rem(7)} ${rem(14)}`,
    position: 'relative',
    zIndex: 1,
    fontWeight: 800,
    fontSize: rem(14),
    borderRadius: '5px',
    transition: '.2s',
    color: 'white',
    [media._sm]: {
      color: 'inherit',
      '::before': {
        transform: 'scale(.9)',
        opacity: 0,
      },
    },
    '::before': {
      content: '""',
      display: 'block',
      width: '100%',
      height: '100%',
      position: 'absolute',
      zIndex: -1,
      left: 0,
      top: 0,
      borderRadius: 'inherit',
      transition: 'inherit',
    },
    ':hover': {
      color: 'white',
      '::before': {
        opacity: 1,
        transform: 'none',
      },
    },
  },
  ({ hoverColor }) => ({ '::before': { background: hoverColor } })
)

export const DayDate = styled.span({
  fontWeight: 800,
  fontSize: rem(14),
  lineHeight: 1.2,
  [media._sm]: {
    fontWeight: 600,
    fontSize: rem(24),
  },
  [media._md]: {
    fontSize: rem(40),
  },
})

DayDate.Detail = styled.span({
  [media._sm]: {
    display: 'block',
    fontWeight: 700,
    fontSize: rem(14),
  },
  [media._md]: { fontSize: rem(18) },
})
